
<template>
    <section>
    <div :ensurer="runAppCheck" class="waiting">Please Wait...</div>
    <div v-if="!$store.state.userLoggedIn" class="refresh-button-box">
        <vs-button class="refresh-button" b666lock @click="fetchAccountData()">Refresh <i class="fas fa-undo"></i></vs-button>
    </div>        
    </section>

</template>

<style lang="scss">
    .waiting {
        text-align:  center;
        margin-top: 50px;
        color: rgb(82, 82, 82);
    }
        .refresh-button-box{
        width: 90%;
        margin: 0px auto;
        display: block;
        margin-top: 25%;
    }
    .refresh-button,.block-button{
        border-radius: 5px;
        width: 100%;
        margin: 0px auto;
        display: block;
    }
</style>

<script>
export default {
    data : function (){
        return {
            name : "app-loading-state"
        }
    },
    computed : {
        runAppCheck : function (){
            let userLoginData = window.localStorage.getItem("userlogindata");
            console.log("userlogindata",userLoginData);
            userLoginData == null ? this.goToHome() : this.fetchAccountData();
            return "true";
        }
    },
    methods : {
        goToHome () {
            this.$router.push("home");
        },
        goToExplore () {
            this.$router.push("explore-main");
        },
        fetchAccountData (){
        let userloggedin = this.$store.state.userLoggedIn;
        if(userloggedin){
            return undefined;
        }
        else {
            let userLoginData = JSON.parse(window.localStorage.getItem("userlogindata"));
            let MID = userLoginData.MID;
            let DID = userLoginData.DID;
            console.log("MID , DID", MID, DID)
            //send request to fetch data here.
            // let url = "";
            let url = "http://localhost/getaccountinfo/"
            this.$vs.loading();
            window.axios.get(url,{
                params : {
                    MID , DID
                }
            }).then((response)=>{
                window.userIsAuthenticated = true;
                this.$vs.loading.close();
                let data = response.data;
                if(data.status == "success"){
                    console.log(data.__userAccountData);
                    this.$router.push("explore-main");                    
                    this.$store.commit('updateloginstatus',response.data.__userAccountData);
                    console.log("updated userlogged in status.");
                }
                else {
                    this.$modal.show("dialog",{
                        title : "Unable to fetch your data.",
                        text : "Reason : This is due to the fact that your data was not found on the database or this device is unauthorized. Please Login again or contact our team with your complain support@mixam.com",
                        buttons : [
                            {
                                title : "Go to login",
                                handler : () => {
                                    this.$modal.hide("dialog");
                                    this.$store.state.commit("openlogin");
                                }
                            }
                        ]
                    })
                }
            }).catch((error)=>{
                this.$vs.loading.close();
                this.$modal.show("dialog",{
                        title : "Unable to fetch your data.",
                        text : "Reason : " + error.message,
                        buttons : [
                            {
                                title : "Okay",
                                handler : () => {
                                    this.$modal.hide("dialog");
                                }
                            }
                        ]
                    })
            })
        }
        },
    }
}
</script>
