<script>

import { TastyBurgerButton } from "vue-tasty-burgers";
import topSearch from "./top-search.vue";

export default {
    data(){
        return {
            name : "topbar",
            ham : {
                buttonType : "elastic",
                size: "xs",
                color : "#ffffff",
                activeColor : "#fff"
            },
            active : this.$store.state.menuOpen

        }
