    methods : {
        onToggle : function(){
            this.$store.commit("updatemenustatus");
       },
        openLogin : function (){
            this.$store.commit("openlogin");
        },
        openSignup : function(){
            this.$store.commit("opensignup");
        },
        openNotification : function (){
            this.$store.commit("updatenotificationopen");
        }
    },
    computed : {

