<template>
    <!--design the post blah blah here.-->
    <section class="notification_container">
        <div><i class="fas fa-caret-up"></i></div>
        <div class="close-container">
            <div class="stroke1"></div>
            <div class="stroke2"></div>
        </div>
        <nofication-item v-for="item in notificationItems" v-bind="item" v-bind:key="item.id"></nofication-item>

        <div>Notification page</div>
    </section>
</template>

<script>
// this is one of the components that will make use of websockets
import noficationItem from "./nofication-item.vue"
export default {
    data : function(){
        return {

        }
    },
    computed : {
        noficationItems :  function () {
            return  this.retrieveItems();
        }
    },
    methods : {
        retrieveItems : async function (){
            let MID = window.localStorage.getItem("userlogindata").MID,DID = window.localStorage.getItem("userlogindata").DID;
            // let url = "/getmynotification/"
            let url = "http://localhost/getmynotification/"
            window.axios.get(url,{
                params : {
                    MID,DID
                }
            }).then((response)=>{
                console.log(response.data);

            }).catch((error)=>{
                console.error(error.message, "notification request failed");
            })
            return  [
                {id: 34},{id:547}
            ];
        }
    },
    components : {
        noficationItem
    }
}
</script>

