<template>
    <div :class="{ 'mobile_search_active' : mobileSearchActive}" class="search-wrapper" v-if="$store.state.userLoggedIn">
        <div class="search-position-ref">
            <p class="search-comp-container">
                <input v-model="searchinput" type="text" placeholder="Search..." class="top-bar-search-input" name="top-seaorch">
                <button @click="searchValidator()" class="search-icon-button"><i class="fas fa-search"></i></button>
            </p>
        </div>
    </div>
</template>
<script>
export default {
    name: "top-search",
    data : function (){
        return {
            searchinput: ""
        }
    },
    computed : {
        mobileSearchActive (){
            return this.$store.state.mobileSearchActive;
        }
    },
    methods: {
        searchValidator : function (){
            if(window.innerWidth < 768){
                console.log("window.innerwidth = ",window.innerWidth,"mobile device" )
                if(this.$store.state.mobileSearchActive){
                    console.log("mobile search is open");
                    if(this.searchinput == ""){
                        this.$modal.show("dialog",{
                            title : "No Input.",
                            text : "Please input something to search for ",
                            buttons : [
                                {
                                    title : "Okay",
                                    handler : ()=>{
                                        this.$modal.hide("dialog");
                                        this.$store.commit("updatemobilesearchactive");
                                        //this.$modal.
                                    }
                                }
                            ]
                        })
                    }
                    else {
                        this.searchFor();
                        this.$store.commit("updatemobilesearchactive");
                    }
                }
                else{
                    this.$store.commit("updatemobilesearchactive");
                    console.log("mobile_State_Active",this.$store.state.mobileSearchActive)
                }
            }
            else {// this is the case when the device is a tab,ipad or laptop
                if(this.searchinput == ""){
                    this.$modal.show("dialog",{
                        title : "No Input.",
                        text : "Please input something to search for ",
                        buttons : [
                            {
                                title : "Okay",
                                handler : ()=>{
                                    this.$modal.hide("dialog");
                                    this.$store.commit("updatemobilesearchactive");
                                    //this.$modal.
                                }
                            }
                        ]
                    })
                }
                else {
                    this.searchFor();
                    this.$store.commit("updatemobilesearchactive");
                }
            }
            /*
            Algorithm for the search.
            Determine if the device is a mobile device.
            if(yes){
                check to see if the vuex store property "mobileseachinputactive is true,"
                if(yes){
                    check if the search input is == "";
                        if(yes){
                            display dialog, please input a search Item
                        }
                        if(no){
                            send search form. with search input and display searchresult page
                            update teh vuex sotre property to false and return it to its original state and
                        }
                }
                if(no){
                    update mobilesearch input active in vuex store which will update the view by adding the class mobile-search-active.
                }
            }
            if(no){
                sendSearchRequest
            }
            */
        },
        searchfor : function () {
            console.log("in searchFor function");
            this.$store.commit("updatecurrentsearchitem",this.searchinput);
            //if router location is not searchresult, then push to the correct location
            console.log("router object : ",this.$router)
            this.$router.push("searchresult");
        }
    }
}
</script>
<style lang="scss">

    .search-wrapper{
        padding: 0px 10px;
    }
    .top-bar-search-input{
    background-color: transparent;
    height: inherit;
    width: 200px;
    border-radius: 25px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    border:0px;
    color: white;
    font-size: 100%;
    padding-left: 12px;
    }
    .top-bar-search-input::placeholder{
        padding: 10px;
        color: white;
        font-size: 100%;
        text-align: right;
        font-style: italic;
        font-family: "Montserrat-Regular";
    }
    .search-icon-button{
        height: 28px;
        padding: 0px 8px;
        border-radius: 30px;
        position: relative;
        left: 1px;
        border: 0px solid;
        background: white;
        color: #09947b;
    }
    .search-comp-container{
        border-radius: 30px;
    background: #5ccdff;
    border: 2px solid white;
    }
    .search-position-ref{
    position: relative;
    bottom: 3px;
    }
    @media (max-width: 650px){
        .top-bar-search-input{
            display: none;
        }
        .search-icon-button{
            left: 0px;
        }
    }

    .mobile_search_active{

    }
</style>