import Vue from 'vue'
import VueRouter from 'vue-router'
import home from '../views/home.vue'
import apploadingstate from "../views/Apploadingstate.vue"
//import explore from "../views/explore.vue"

Vue.use(VueRouter)

const routes = [
  {
    path: '/home',
    name: 'home',
    component: home
  },
  {
    path : '/explore-main',
    name : 'explore-main',
    component : () => import ('../views/explore-main.vue'),
  },
  {
    path : '/',
    name : 'apploadingstate',
    component : apploadingstate,
  },
  {
    path: "/searchresult",
    name : "searchresult",
    component : () => import ("../views/search-result.vue"),
  },
  {
    path: "/create-main",
    name : "create-main",
    component : ()=> import("../views/create-main.vue")
  },
  {
    path: "/create-post",
    name : "create-post",
    component : () => import("../views/create-post.vue")
  },
  {
    path: "/create-article",
    name : "create-article",
    component : () => import("../views/create-article.vue")
  },
  {
    path: "/create-course",
    name : "create-course",
    component : () => import("../views/create-business-course.vue")
  },
  {
    path: "/create-product-showcase",
    name : "create-product-showcase",
    component : () => import("../views/create-product-showcase.vue")
  },
  /*
  {
    path: "",
    name : "",
    component : () => import("../views/")
  },
  */
]

const router = new VueRouter({
  routes
})
