<template>
    <div class="profile-builder">
        <modal :clickToClose="false" name="profilebuilder" :adaptive="true" height="auto" :max-width="maxWidth" width="90%">
            <section  :title="profileBuilderOpen">
                <!--template-->
                <div  class="profile-builder hiddenitem item-template-container">
                    <!--header-section-->
                    <div class="profile-builder header">
                        <div class="profile-builder-logo-container">
                            <div>
                                <div class="logo-container">
                                    <p class="company-name">Mixam <img class="mixam-logo" src="../assets/logo.png"></p>
                                    <p class="company-name-follow">BUSINESS</p>
                                </div>
                            </div>
                        </div>
                        <div class="profile-builder title-container">
                            <p class="profile-builder item-title">Your Title Here</p>
                        </div>
                    </div>
                    <!--body section-->
                    <section class="profile-builder body-section-container">
                        <div class="profile-builder interactive-image-container">
                            <div>
                                <img src="../assets/4630807.jpg" alt="interactive image">
                            </div>
                        </div>
                        <div class="profile-builder main-content-container">
                            <div>
                                <div class="profile-builder content-container">

                                </div>
                                <div class="profile-builder buttons-container">
                                    <button class="profile-builder-button">Button Value</button>
                                </div>
                            </div>
                        </div>
                    </section>
                </div>
                <!--Welcome, Choose a username, upload a profile photo, what kind of business are you into.-->
            <!--Welcome-->
                <div :class="{hiddenitem : welcome}" class="animate__animated animate__fadeIn profile-builder item-template-container">
                    <!--header-section-->
                    <div class="profile-builder header">
                        <div class="profile-builder-logo-container">
                            <div>
                                <div class="logo-container">
                                    <p class="company-name">Mixam <img class="mixam-logo" src="../assets/logo.png"></p>
                                    <p class="company-name-follow">BUSINESS</p>
                                </div>
                            </div>
                        </div>
                        <div class="profile-builder title-container">
                            <p class="profile-builder item-title">Welcome to Mixam Business</p>
                        </div>
                    </div>
                    <!--body section-->
                    <section class="profile-builder body-section-container">
                        <div class="profile-builder interactive-image-container">
                            <div>
                                <img src="../assets/4630807.jpg" alt="interactive image">
                            </div>
                        </div>
                        <div class="profile-builder main-content-container">
                            <div>
                                <div id="welcome-content" class="profile-builder content-container">
                                    <p>Hi,</p>
                                    <p>We are so elated that you joined us. Let's get this adventure started </p>
                                </div>
                                <div class="profile-builder buttons-container">
                                    <button @click="moveToChooseUsername()" class="profile-builder-button">Next <i class="fas fa-arrow-right"></i></button>
                                </div>
                            </div>
                        </div>
                    </section>
                </div>
            <!-- Choose Username -->
                <div :class="{hiddenitem : chooseusername}" class=" profile-builder item-template-container">
                    <!--header-section-->
                    <div class="profile-builder header">
                        <div class="profile-builder-logo-container">
                            <div>
                                <div class="logo-container">
                                    <p class="company-name">Mixam <img class="mixam-logo" src="../assets/logo.png"></p>
                                    <p class="company-name-follow">BUSINESS</p>
                                </div>
                            </div>
                        </div>
                        <div class="profile-builder title-container">
                            <p class="profile-builder item-title">Choose a username</p>
                        </div>
                    </div>
                    <!--body section-->
                    <section class="animate__animated animate__fadeIn profile-builder body-section-container">
                        <div class="profile-builder interactive-image-container">
                            <div>
                                <img src="../assets/4630807.jpg" alt="interactive image">
                            </div>
                        </div>
                        <div class="profile-builder main-content-container">
                            <div>
                                <div class="profile-builder content-container">
                                    <p class="usernametext">Username:</p>
                                    <p>
                                        <input v-model="username" type="text" class="usenameinput">
                                    </p>
                                </div>
                                <div class="profile-builder buttons-container">
                                    <button class="profile-builder-button" @click="sendUsername()">Next</button>
                                </div>
                            </div>
                        </div>
                    </section>
                </div>
            <!-- Upload Profile Photo -->
                <div :class="{hiddenitem : uploadphoto}" class="profile-builder item-template-container">
                    <!--header-section-->
                    <div class="profile-builder header">
                        <div class="profile-builder-logo-container">
                            <div>
                                <div class="logo-container">
                                    <p class="company-name">Mixam <img class="mixam-logo" src="../assets/logo.png"></p>
                                    <p class="company-name-follow">BUSINESS</p>
                                </div>
                            </div>
                        </div>
                        <div class="profile-builder title-container">
                            <p class="profile-builder item-title">Upload a Profile Photo</p>
                        </div>
                    </div>
                    <!--body section-->
                    <section class="animate__animated animate__fadeIn profile-builder body-section-container">
                        <div class="profile-builder interactive-image-container">
                            <div>
                                <img src="../assets/4630807.jpg" alt="interactive image">
                            </div>
                        </div>
                        <div class="profile-builder main-content-container">
                            <div>
                                <div class="profile-builder content-container">
                                    <p class="usernametext">Upload your profile photo here</p>
                                    <form id="profilephoto" enctype="multipart/form-data">
                                    <p class="uploadinput-container">
                                        <input name="profilephotoupload" type="file" class="photoupload">
                                    </p>
                                    </form>

                                </div>
                                <div id="upload" class="profile-builder buttons-container">
                                    <button @click="moveToBusinessType()" id="skip-btn-upload" class="profile-builder-button">Skip</button><button @click="sendProfilePhoto()" class="profile-builder-button">Next <i class="fas fa-arrow-right"></i></button>
                                </div>
                            </div>
                        </div>
                    </section>
                </div>
            <!-- What kind of business are you into? -->
                <div :class="{hiddenitem : businesstype}" class=" profile-builder item-template-container">
                    <!--header-section-->
                    <div class="profile-builder header">
                        <div class="profile-builder-logo-container">
                            <div>
                                <div class="logo-container">
                                    <p class="company-name">Mixam <img class="mixam-logo" src="../assets/logo.png"></p>
                                    <p class="company-name-follow">BUSINESS</p>
                                </div>
                            </div>
                        </div>
                        <div class="profile-builder title-container">
                            <p class="profile-builder item-title">What business are you into?</p>
                        </div>
                    </div>
                    <!--body section-->
                    <section class="animate__animated animate__fadeIn profile-builder body-section-container">
                        <div class="profile-builder interactive-image-container">
                            <div>
                                <img src="../assets/4630807.jpg" alt="interactive image">
                            </div>
                        </div>
                        <div class="profile-builder main-content-container">
                            <div>
                                <div class="profile-builder content-container">
                                    <p class="usernametext">
                                        What kind of business are you into, do you sell a product or render a service?
                                    </p>
                                     <p>
                                        <textarea v-model="businesstypeinput" type="text" class="usenameinput textarea"> </textarea>
                                    </p>
                                </div>
                                <div class="profile-builder buttons-container">
                                    <button @click="sendBusinessType()" class="profile-builder-button">Finish <i class="fas fa-arrow-right"></i></button>
                                </div>
                            </div>
                        </div>
                    </section>
                </div>

            </section>
        </modal>
    </div>
</template>

<script>

export default {
    data () {
        return {
            name : "profile-builder-and-welcome",
            maxWidth : 400,
            username: "",
            welcome : false,
            businesstype: true,
            uploadphoto : true,
            chooseusername : true,
            businesstypeinput : ""
        }
    },
    computed : {
        profileBuilderOpen (){
            console.log("profilebuilderopen", this.$store.state.profileBuilderOpen)
            return this.$store.state.profileBuilderOpen;
        },

    },
    methods : {
        openProfileBuilder(){
            this.$modal.show("profilebuilder")
        },
        closeProfileBuilder(){
            this.$modal.hide("profilebuilder")
        },
        moveToChooseUsername(){
            this.welcome=true;
            this.chooseusername = false;
        },
        moveToUploadPhoto(){
            this.chooseusername = true;
            this.uploadphoto = false;
        },
        moveToBusinessType (){
            this.uploadphoto = true;
            this.businesstype = false;
        },
        finish(){
            this.$store.commit("updateprofilebuilder", false);
        },
        sendUsername(){
            this.$modal.hide("dialog");
            this.$vs.loading();
            let url = "http://localhost/submitdatainit/";
            let MID = this.$store.state.userAccountInfo.__mid;
            let DID = JSON.parse(window.localStorage.getItem("userlogindata")).DID;
            console.log("MID and DID",MID,DID)
            // let url = "/submitdatainit";
            window.axios.post(url,{
                params : {
                    action : "addusername",
                    data : this.username,
                    MID,
                    DID
                }
            }).then((response)=>{
                this.$vs.loading.close();
                let data = response.data;
                console.log("username response");
                if(data.status == "success"){
                    console.log("successfully added username");
                    this.moveToUploadPhoto();
                }
                else {
                    this.$modal.show("dialog",{
                    title : "unable to add your username info",
                    text : "We are unable to add your username to our database now due to some issues we are experiencing now. Our team is working to get you back on track. You skip this section for now and edit your username later in your profile settings or try again with the button.",
                    buttons  : [
                            {
                                title : "Try Again",
                                handler : () =>{
                                    this.$modal.hide("dialog");
                                    this.sendUsername();
                                }
                            },
                            {
                                title : "Skip This Section",
                                handler : () =>{
                                    this.$modal.hide("dialog");
                                    this.moveToUploadPhoto();
                                }
                            }
                        ]
                    })
                }
            }).catch((error)=>{
                this.$vs.loading.close();
                console.error(error);
                this.$modal.show("dialog",{
                    title : "Unable to add your username info",
                    text : "Reason : No internet connection. Please turn your mobile data on or try connecting to a wifi.",
                    buttons  : [
                        {
                            title : "Try Again",
                            handler : () =>{
                                this.$modal.hide("dialog");
                                this.sendUsername();
                            }
                        }
                    ]
                })
            })
        },
        sendProfilePhoto(){
            this.$vs.loading();
            this.$modal.hide("dialog");
            let action = "addprofilephoto";
            let MID = this.$store.state.userAccountInfo.__mid;
            let DID = JSON.parse(window.localStorage.getItem("userlogindata")).DID;
            console.log("action, MID and DID",MID,DID,action)
            let url = "http://localhost/submitdatainit/"
            // let url = "/submitdatainit";
           console.log(url);
            let formElement = document.getElementById("profilephoto");
            let formData = new FormData(formElement);
            formData.append("params",JSON.stringify({action,MID,DID}))
            window.axios.post(url,formData,{
                headers : {
                    "Content-Type" : "multipart/form-data"
                }
            }).then((response)=>{
                this.$vs.loading.close();
                let data = response.data;
                if(data.status == "success"){
                    console.log("successfully uploaded proile photo",data);
                    this.moveToBusinessType();
                }
                else {
                    this.$modal.show("dialog",{
                    title : "unable to upload Profile Photo Now.",
                    text : "We are unable to upload your profile photo to our database due to some issues we are experiencing now. Our team is working to get you back on track. You skip this section for now and upload your profile photo later in your profile settings or try again with the button.",
                    buttons  : [
                            {
                                title : "Try Again",
                                handler : () =>{
                                    this.$modal.hide("dialog");
                                    this.sendProfilePhoto();
                                }
                            },
                            {
                                title : "Skip This Section",
                                handler : () =>{
                                    this.$modal.hide("dialog");
                                    this.moveToBusinessType();
                                }
                            }
                        ]
                    })
                }
            }).catch((error)=>{
                this.$vs.loading.close();
                console.log("unable to send profile photo");
                this.$modal.show("dialog",{
                    title : "unable to upload your profile photo",
                    text : "Reason : " + error.message,
                    buttons  : [
                        {
                            title : "Try Again",
                            handler : () =>{
                                this.$modal.hide("dialog");
                                this.sendProfilePhoto();
                            }
                        }
                    ]
                });
            })
            /*
            let request = new XMLHttpRequest();
            request.open("POST", url);
            request.send(formData);
            let handleSuccess =  () =>{
                this.$vs.loading.close()
                console.log("profile photo sent");
                console.log(request.response, "response for profile upload");
                this.moveToBusinessType();
            }
            let handleError = () =>{
                //action still loading;
                this.$vs.loading.close();
                console.log("unable to send profile photo");
                this.$modal.show("dialog",{
                    title : "unable to upload your profile photo",
                    text : "Reason : No internet connection. Please check your network.",
                    buttons  : [
                        {
                            title : "Try Again",
                            handler : () =>{
                                this.sendProfilePhoto();
                            }
                        }
                    ]
                });
            }
            request.onload = handleSuccess;
            request.onerror = handleError;*/
        },
        sendBusinessType(){
            this.$modal.hide("dialog")
            let url = "http://localhost/submitdatainit/";
            let MID = this.$store.state.userAccountInfo.__mid;
            let DID = JSON.parse(window.localStorage.getItem("userlogindata")).DID;
            console.log("MID and DID",MID,DID)
            // let url = "/submitdatainit";
            window.axios.post(url,{
                params : {
                    action : "adduserbusiness",
                    data : this.businesstypeinput,
                    MID ,
                    DID
                }
            }).then((response)=>{
                this.$vs.loading.close();
                let data = response.data;
                if(data.status == "success"){
                    console.log("added user businesstype successfully", data);
                    this.finish();
                }
                else {
                    this.$modal.show("dialog",{
                    title : "Unable to save your business Info.",
                    text : "We are unable to save your business Info to our database now due to some issues we are experiencing now. Our team is working to get you back on track. You skip this section for now and edit your Business Info later in your profile settings or try again with the button.",
                    buttons  : [
                            {
                                title : "Try Again",
                                handler : () =>{
                                    this.sendBusinessType();
                                }
                            },
                            {
                                title : "Skip This Section",
                                handler : () =>{
                                    this.$modal.hide("dialog");
                                    this.finish();
                                }
                            }
                        ]
                    })
                }
            }).catch((error)=>{
                this.$vs.loading.close();
                console.error(error);
                this.$modal.show("dialog",{
                    title : "unable to add your business info",
                    text : "Reason : No internet connection. Please check your network.",
                    buttons  : [
                        {
                            title : "Try Again",
                            handler : () =>{
                                this.sendBusinessType();
                            }
                        }
                    ]
                })
            })
        }
    },
    watch : {
        profileBuilderOpen(old,value){
            old == true ? this.openProfileBuilder() : this.closeProfileBuilder();
            value;
            console.log("new value for profileBuilderOpen", old);
        }
    }
}
</script>

<style lang="scss">
    .profile-builder{
        font-family : "Antipasto-Bold";
    }
    .profile-builder.header{
        width: 90%;
        border-radius: 10px;
        margin: 18px auto;
        padding-top: 5px;
        background-color: #3ac0fd;
    }
    .profile-builder-logo-container{
        width: max-content;
        height: max-content;
        background-color: white;
        border-radius: 10px;
        margin: 5px;
        padding: 0px 5px 0px 5px;
    }
    .profile-builder .company-name-follow {
        color: #03a9f4;
    }
    .interactive-image-container img{
        width: 40%;
        margin: 0px auto;
        display: block;
    }
    .profile-builder.main-content-container {
        width: 90%;
        border: 1px solid #03A9F4;
        margin: 5px auto;
        height: 200px;
        margin-bottom: 30px;
        border-radius: 12px;
    }
    .profile-builder .item-title{
        color: white;
        padding-left: 7px;
        font-size : 22px;
    }
    .profile-builder-button {
        border: 0px solid;
        background: #3dab46;
        color: white;
        padding: 5px 10px 5px 10px;
        border-radius: 4px;
        display: block;
        margin: 0px auto;
    }
    #upload{
        width: max-content;
        margin: 0px auto;
    }
    #upload button{
        display: inline-block;
        margin: 5px;
    }
    #skip-btn-upload{
        background: #143539;
    }
    #welcome-content{
        text-align: center;
        margin-bottom: 15px;
        margin-top: 15%;
        font-family: "Montserrat-Bold";
        padding: 0px 10px 0px 10px;
    }
    .hiddenitem{
        display : none;
    }
    .usenameinput{
        width: 90%;
        height: 35px;
        border-radius: 5px;
        border: 2px solid #b0b0b0;
        margin: 0px auto;
        display: block;
        margin-bottom: 10px;
    }
    .usernametext{
        width: 90%;
        margin: 0px auto;
        margin-top: 50px !important;
    }
    .textarea{
        height: 50px;
    }
    .uploadinput-container{
        width: 90%;
    margin: 0px auto;
    margin-bottom: 10px !important;
    }
    .photoupload{
        padding: 10px;
    border: 1px solid gray;
    width: 100%;
    }
    </style>